import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  mode: 'history',//访问地址去掉#
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },

    {
      path: '/login',
      component: () => import('@/view/login/Login'),
      hidden: true
    },
    {
      path: '/',
      component: () => import('@/view/home/Public'),
      hidden: true
    },
  ]
})



